div.favoritesCat {
  float: right;
}
  .favoritesCat input[type="checkbox"] {
    display: none;
  }

  div.BigfavoritesCat{
    display: block;
    border: 0.1px solid rgba(0, 0, 0, 0);
    margin: auto;
  }
  
  .favoritesCat label {
    border: 1px solid #fff;
    display: block;
    position: relative;
    margin: 5px;
    cursor: pointer;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  .favoritesCat label::before {
    background-color: white;
    color: white;
    content: " ";
    display: block;
    border-radius: 50%;
    border: 1px solid grey;
    position: absolute;
    top: -5px;
    left: -5px;
    width: 25px;
    height: 25px;
    text-align: center;
    line-height: 28px;
    transition-duration: 0.4s;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  
  .favoritesCat label img {
    width: 80px;
    height: 80px;
    transition-duration: 0.2s;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  
  .favoritesCat :checked+label {
    border-color: #ddd;
  }
  
  .favoritesCat :checked+label::before {
    content: "\2713";
    background-color: grey;
    -webkit-transform: scale(1);
            transform: scale(1);
    z-index: 1;
  }
  
  .favoritesCat :checked+label img {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    box-shadow: 0 0 5px #333;
    z-index: -1;
  }
